"use client";

import Link from "next/link";

const campeonatos = [
  {
    titulo: "Copa Caloura 2025",
    descricao:
      "Campeonato entre turmas de primeiro ano. Próxima partida: Informática x Eletrotécnica.",
  },
  {
    titulo: "Interturmas 2025",
    descricao:
      "Times de todos os cursos disputando o título de melhor equipe do campus.",
  },
];

const classificacao = [
  {
    posicao: "1º",
    equipe: "Informática",
    pontos: 12,
    vitorias: 4,
    derrotas: 0,
  },
  {
    posicao: "2º",
    equipe: "Eletrotécnica",
    pontos: 9,
    vitorias: 3,
    derrotas: 1,
  },
  { posicao: "3º", equipe: "Mecânica", pontos: 6, vitorias: 2, derrotas: 2 },
  { posicao: "4º", equipe: "Edificações", pontos: 3, vitorias: 1, derrotas: 3 },
];

export default function Campeonatos() {
  return (
    <div>
      <header>
        <div className="logo">CE Sports</div>
        <nav id="navbar">
          <Link href="/">Home</Link>
          <Link href="/projetos">Projetos</Link>
          <Link href="/noticias">Notícias</Link>
          <button className="btn-login" id="btnLogin">
            Login
          </button>
        </nav>
      </header>

      <main
        className="main-content"
        style={{ background: "var(--cinza-claro)", minHeight: "80vh" }}
      >
        <Link href="/" className="btn-voltar">
          ← Voltar à Home
        </Link>
        <h1
          style={{
            margin: "32px 0 20px",
            fontSize: "2rem",
            color: "var(--texto-escuro)",
            fontWeight: 600,
          }}
        >
          Campeonatos em Andamento
        </h1>
        <section
          className="preview-cards"
          style={{ display: "flex", gap: "24px", marginBottom: "32px" }}
        >
          {campeonatos.map((camp, idx) => (
            <div
              key={idx}
              className="preview-card"
              style={{
                background: "var(--branco)",
                borderRadius: "8px",
                boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
                padding: "24px",
                flex: 1,
                minWidth: 300,
              }}
            >
              <div style={{ fontWeight: "bold", fontSize: "1.1rem" }}>
                {camp.titulo}
              </div>
              <div style={{ marginTop: 6, color: "#333" }}>
                {camp.descricao}
              </div>
              <button
                className="btn-outline"
                style={{
                  marginTop: 16,
                  border: "2px solid var(--azul-claro)",
                  color: "var(--azul-claro)",
                  background: "none",
                  padding: "8px 16px",
                  borderRadius: "6px",
                  fontWeight: "bold",
                  cursor: "pointer",
                }}
              >
                Ver Detalhes
              </button>
            </div>
          ))}
        </section>

        <h2
          style={{
            fontSize: "1.3rem",
            color: "var(--azul-escuro)",
            marginTop: "36px",
            marginBottom: "18px",
          }}
        >
          Tabela de Classificação - Copa Caloura
        </h2>
        <table
          className="tabela-classificacao"
          style={{
            width: "100%",
            borderCollapse: "collapse",
            background: "var(--branco)",
            boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
            borderRadius: "8px",
            overflow: "hidden",
          }}
        >
          <thead>
            <tr
              style={{
                background: "var(--azul-claro)",
                color: "var(--branco)",
              }}
            >
              <th style={{ padding: "12px" }}>Posição</th>
              <th style={{ padding: "12px" }}>Equipe</th>
              <th style={{ padding: "12px" }}>Pontos</th>
              <th style={{ padding: "12px" }}>Vitórias</th>
              <th style={{ padding: "12px" }}>Derrotas</th>
            </tr>
          </thead>
          <tbody>
            {classificacao.map((row, idx) => (
              <tr
                key={idx}
                style={{
                  borderBottom: "1px solid #ddd",
                  background: idx % 2 === 0 ? "#fff" : "#f8f9fa",
                }}
              >
                <td style={{ padding: "10px 12px" }}>{row.posicao}</td>
                <td style={{ padding: "10px 12px" }}>{row.equipe}</td>
                <td style={{ padding: "10px 12px" }}>{row.pontos}</td>
                <td style={{ padding: "10px 12px" }}>{row.vitorias}</td>
                <td style={{ padding: "10px 12px" }}>{row.derrotas}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </main>

      <footer
        style={{
          background: "var(--azul-escuro)",
          color: "var(--branco)",
          textAlign: "center",
          padding: "15px",
          fontSize: "0.9rem",
          marginTop: "auto",
        }}
      >
        &copy; 2025 CE Sports — CEFET-MG | Todos os direitos reservados
      </footer>
    </div>
  );
}
